.qna-body {
  background: #262626;
  border: 1px solid #292929;
  padding: 20px;
  z-index: 99;
}

.qna-submit {
  background: #262626;
  border: 1px solid #292929;
  border-radius: 3px;
  margin-top: 10px;
  padding: 5px;
  z-index: 99;
}

#qna-text {
  width: 100%;
  border: none;
  outline: none;
  resize: none;
  background-color: transparent;
}

@body-background: #181818;@primary-color: #663399;@link-color: #663399;@font-size-base: 14px;@font-family: Roboto, sans-serif;@menu-dark-item-active-bg: transparent;@border-radius-base: 3px;